import React, { Component } from 'react'
import Link, { navigateTo } from 'gatsby-link'

class SearchBox extends Component {
    state = {
        query: ''
    }

    render() {
        const inputStyle = {
            width: '80%',
            height: '100%',
            backgroundColor: 'white',
            padding: '0 0.5rem',
            fontSize: '1rem',
            boxSizing: 'border-box',
        }

        return (
            <form style={{height: '2rem'}} onSubmit={this.handleSubmit}>
                <input 
                    type="text" 
                    placeholder="Search clinical updates"
                    style={inputStyle}
                    onChange={this.handleChange}
                />
                <button 
                    style={{
                        height: '100%', 
                        width: '20%', 
                        boxSizing: 'border-box',
                        cursor: 'pointer',
                    }} 
                    type="submit" value="">
                    <svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" width="32" height="32" viewBox="0 0 32 32">
                        <path fill="#fff" d="M31.008 27.231l-7.58-6.447c-0.784-0.705-1.622-1.029-2.299-0.998 1.789-2.096 2.87-4.815 2.87-7.787 0-6.627-5.373-12-12-12s-12 5.373-12 12 5.373 12 12 12c2.972 0 5.691-1.081 7.787-2.87-0.031 0.677 0.293 1.515 0.998 2.299l6.447 7.58c1.104 1.226 2.907 1.33 4.007 0.23s0.997-2.903-0.23-4.007zM12 20c-4.418 0-8-3.582-8-8s3.582-8 8-8 8 3.582 8 8-3.582 8-8 8z"></path>
                    </svg>
                </button>
            </form>
        )
    }

    handleChange = (event) => {
        this.setState({
            query: event.target.value
        })
    }

    handleSubmit = (event) => {
        event.preventDefault();
        navigateTo(`/search?q=${this.state.query}`);
        if(this.props.menuOpen) { this.props.toggleNav() }
    }
}

export default SearchBox



