import React, { Component } from 'react'
import Link from 'gatsby-link'

class TopicLink extends Component {
    render() {
        return (
            <div style={{
                display: 'block',
            }}>
                <span style={{color: 'grey'}}>{this.props.node.fields.path}</span>
                <Link
                    to={`/${this.props.node.fields.path}`}
                    style={{
                        color: 'blue',
                        marginTop: '0'
                    }}
                >
                    <h3>{this.props.node.title}</h3>
                </Link>
            </div>
        )
    }
}

export default TopicLink



