import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'

import Header from '../components/Header.js'
import Sidebar from '../components/Sidebar.js'
import BodyListener from '../components/BodyListener'
import TopicLink from '../components/TopicLink'
import Footer from '../components/Footer'
import './style.css'
// import './hamburgers.css'


class TemplateWrapper extends Component {
  constructor(props){
    super(props);
  }

  state = {
    menuOpen: false
  }
  
  render(){
    return (
      <div>
        <Helmet
          title="Ipas Clinical Updates"
          meta={[
            // { name: 'keywords', content: 'sample, something' },
            // { name: 'description', content: 'Sample' },
          ]}
        />
        <Header 
          toggleNav={this.toggleNav}
          menuOpen={this.state.menuOpen}
        />
        <Sidebar 
          menuPages={this.props.data.allWordpressWpApiMenusMenusItems.edges[0].node.items}
          topicPages={this.props.data.allWordpressWpTopics.edges}
          toggleNav={this.toggleNav}
          menuOpen={this.state.menuOpen}
        />
        <BodyListener
          toggleNav={this.toggleNav}
          menuOpen={this.state.menuOpen}
        >
          {this.props.children()}

          <Footer />
        </BodyListener>
      </div>
    )
  }

  toggleNav = () => {
    this.setState({ menuOpen: !this.state.menuOpen });
  }
}

TemplateWrapper.propTypes = {
  children: PropTypes.func,
}

export default TemplateWrapper

export const menuQuery = graphql`
  query menuItemsQuery{
    allWordpressWpApiMenusMenusItems(filter: {slug: {eq: "sidebar"}}) {
      edges{
        node{
          wordpress_id
          name
          slug
          count
          items {
            wordpress_id
            order
            wordpress_parent
            title
            url
            object_slug
          }
        }
      }
    }
    allWordpressWpTopics{
      edges{
          node{
              wordpress_id
              name
              slug
          }
      }
    }
  }
`



