import React, { Component } from 'react'
import Link from 'gatsby-link'
import SearchBox from '../components/SearchBox'

class Sidebar extends Component {
    render() {
        const navStyle = {
            "width": "375px",
            "maxWidth": "100%",
            "position": "absolute",
            "left": this.props.menuOpen ? "0px" : "-377px",
            "top": "4em",
            "bottom": "0",
            "boxSizing": "border-box",
            "zIndex": "100",
            "backgroundColor": "#00AFDB",
            "boxShadow": "3px 0 2px rgba(0, 0, 0, .25)",
            "overflowY": "scroll"
        }

        const linkStyle = {
            "textDecoration": "none",
            "display": "block",
            "color": "#fff",
            "padding": ".5em",
            "borderBottom": "1px solid #d3d3d3",
            "margin": "0",
            "transition": "all .3s ease-in-out",
            "fontSize": "1rem",
            "lineHeight": "1.5rem",
        }

        return (
            <nav style={navStyle}>
                <SearchBox menuOpen={this.props.menuOpen} toggleNav={this.props.toggleNav}/>
                {this.props.menuPages && this.props.menuPages.map((page) => {
                    return (
                        <Link to={`${page.url}`} 
                            key={page.wordpress_id} 
                            style={linkStyle}
                            onClick={this.props.toggleNav}>
                            {page.title}
                        </Link>
                    )
                })}
                {this.props.topicPages && this.props.topicPages.map((topic) => {
                    return (
                        <Link to={`/${topic.node.slug}`} 
                            key={topic.node.wordpress_id} 
                            style={linkStyle}
                            onClick={this.props.toggleNav}>
                            {topic.node.name}
                        </Link>
                    )
                })}
            </nav>
        )
    }
}

export default Sidebar



