import React, { Component } from 'react'

class BodyListener extends Component {
    render() {
        return (
            <main
                style={{
                    height: '100%',
                    width: '100%'
                }}
                onClick={this.closeMenuIfOpen}>
                {this.props.children}
            </main>
        )
    }

    closeMenuIfOpen = (event) => {
        event.preventDefault();
        if (this.props.menuOpen) {
            this.props.toggleNav();
        }
    }
}

export default BodyListener



